	import {
		mapState
	} from 'vuex';
	import uniNumberBox from '@/components/uni-number-box.vue'
	import common from '@/store/common.js'

	export default {
		components: {
			uniNumberBox
		},
		data() {
			return {
				total: 0, //总价格
				allChecked: false, //全选状态  true|false
				empty: false, //空白页现实  true|false
				cartList: [],
				discounted: "0.00",
				userInfo: {},
				cartIdList: []
			};
		},
		onLoad() {
			this.loadData();
		},
		watch: {
			//显示空白页
			cartList(e) {
				let empty = e.length === 0 ? true : false;
				if (this.empty !== empty) {
					this.empty = empty;
				}
			}
		},
		computed: {
			...mapState(['hasLogin'])
		},
		methods: {
			getUser() {
				let that = this;
				that.userInfo = common.getGlobalUserInfo();
				// console.log("本地UserInfo",this.userInfo)
			},
			//请求数据
			async loadData() {
				this.getUser();
				// let list = await this.$api.json('cartList'); 

				uni.request({
					url: this.apiServer + '/cart/loadCart',
					method: 'POST',
					dataType: "json",
					data: {
						"customerID": this.userInfo.ID,
					},
					success: (res) => {
						const result = res.data
						// console.log("后台返回数据",result)
						
						console.log(this.cartIdList)
						let list = result;
						let cartList = list.map(item => {
							item.checked = true;
							return item;
						});
						this.cartList = cartList;
						console.log("this.cartList", this.cartList);
						this.calcTotal(); //计算总价
					}
				});

			},
			//监听image加载完成
			onImageLoad(key, index) {
				this.$set(this[key][index], 'loaded', 'loaded');
			},
			//监听image加载失败
			onImageError(key, index) {
				this[key][index].image = '/static/errorImage.jpg';
			},
			navToLogin() {
				uni.navigateTo({
					url: '/pages/public/loginByPhone'
				})
			},
			//选中状态处理
			check(type, index) {
				if (type === 'item') {
					this.cartList[index].checked = !this.cartList[index].checked;
				} else {
					const checked = !this.allChecked
					const list = this.cartList;
					list.forEach(item => {
						item.checked = checked;
					})
					this.allChecked = checked;
				}
				this.calcTotal(type);
			},
			//数量
			numberChange(data) {
				this.cartList[data.index].commodity_quantity = data.number;
				//修改数据库Cart表中commodity_quantity属性
				uni.request({
					url: this.apiServer + '/cart/updateQuantity',
					method: 'POST',
					dataType: "json",
					data: {
						"commodityQuantity": data.number,
						"commodityID": this.cartList[data.index].commodity_id,
						"customerID": this.userInfo.ID
					},
					success: (res) => {
						const result = res.data
						console.log("后台返回数据", result)
						this.calcTotal(); //计算总价
					}
				});
				this.calcTotal();
			},
			//删除
			deleteCartItem(index) {
				let list = this.cartList;
				let row = list[index];
				let id = row.id;
				console.log(index);
				console.log(this.cartList[index]);
				// console.log("this.cartList[index - 1].commodity_id : ",this.cartList[index].commodity_id)
				this.cartList.splice(index, 1);
				//删除数据库Cart表相应记录，采用的方法是设置isDelete字段为1
				uni.request({
					url: this.apiServer + '/cart/deleteCartItem',
					method: 'POST',
					dataType: "json",
					data: {
						"commodityID": this.cartList[index].commodity_id,
						"commodityQuantity": this.cartList[index].commodity_quantity,
						"customerID": this.userInfo.ID
					},
					success: (res) => {
						const result = res.data
						console.log("后台返回数据", result)
						this.calcTotal(); //计算总价
					}
				});

				this.calcTotal();
				uni.hideLoading();
			},
			//清空
			clearCart() {
				uni.showModal({
					content: '清空购物车？',
					success: (e) => {
						if (e.confirm) {
							// this.cartList = [];
							for (var i = 0; i < this.cartList.length; i++) {
								this.cartIdList.push(this.cartList[i].cartID);
							}
							console.log(this.cartIdList);
							uni.request({
								url: this.apiServer + '/cart/clearCart',
								method: 'POST',
								dataType: "json",
								data: this.cartIdList,
								success: (res) => {
									this.cartIdList = [];
									this.loadData();
									this.calcTotal(); //计算总价
								},
								fail: () => {
									this.cartIdList = [];
									this.loadData();
									this.calcTotal(); //计算总价
								}
							});
						}
					}
				})
			},
			//计算总价
			calcTotal() {
				let list = this.cartList;
				if (list.length === 0) {
					this.empty = true;
					return;
				}
				let total = 0;
				let checked = true;
				list.forEach(item => {
					if (item.checked === true) {
						total += item.price * item.commodity_quantity;
					} else if (checked === true) {
						checked = false;
					}
				})
				this.allChecked = checked;
				this.total = Number(total.toFixed(2));
			},
			//创建订单
			createOrder() {
				let list = this.cartList;
				let goodsData = [];
				list.forEach(item => {
					if (item.checked) {
						goodsData.push({
							specs_name: item.specs_name,
							commodity_quantity: item.commodity_quantity
						})
					}
				})

				uni.navigateTo({
					url: `/pages/order/createOrder?data=${JSON.stringify({
  						goodsData: goodsData
  					})}`
				})
				this.$api.msg('跳转下一页 sendData');
			}
		}
	}
